import React, {FC} from 'react';
import styled from 'styled-components';
import {cover} from 'polished';
import theme from '../../theme';

interface IProps {
  className?: string;
  id: string | number;
}

const Holder = styled.article`
  max-width: 300px;


  position: relative;
  height: 0;
  padding-bottom: calc(100% - 75px);
  margin-bottom: 6.6em;
  display: block;
  box-sizing: border-box;
  background: white;
`;

const Intro = styled.div`
  box-sizing: border-box;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  padding: 10px 16px;
  margin: 0;
  z-index: 10;
  transition: all 0.3s;
  color: ${theme.palette.text.terciary};
  background-color: ${theme.palette.background.secondary};
`;

const ContentHolder = styled.div`
  position: absolute;
  top: 8px;
  right: 8px;
  bottom: 8px;
  left: 8px;
  transition: all 0.3s;
`;

const ImageHolder = styled.div`
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  transition: all 0.3s;
`;

const Image = styled.img`
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
`;

const Description = styled.div`
  position: absolute;
  top: 100%;
  right: 30px;
  left: 30px;
  bottom: 54px;
  overflow: hidden;
  opacity: 0;
  filter: alpha(opacity=0);
  transition: all 1.2s;
`;

//Experimental approach, using checkbox to toggle
const Input = styled.input`
  display: none;
`;

const ExpandButton = styled.label`

  position: absolute;
  right: 16px;
  top: 15px;
  z-index: 20;
  width: 60px;
  height: 45px;

  margin: 50px auto;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;

  span {
   display: block;
   position: absolute;
   height: 4.5px;
   width: 100%;
   background: #d3531a;
   border-radius: 9px;
   opacity: 1;
   left: 0;
   transform: rotate(0deg);
   transition: .25s ease-in-out;
  }

  span:nth-child(1) {
    top: 0px;
    transform-origin: left center;
  }

  span:nth-child(2) {
    top: 9px;
    transform-origin: left center;
  }
  span:nth-child(3) {
    top: 18px;
    transform-origin: left center;
  }

  ${Input}:checked ~ & span:nth-child(1) {
    transform: rotate(45deg);
    top: -1.5px;
    left: 4px;
  }

  ${Input}:checked ~ & span:nth-child(2) {
    width: 0%;
    opacity: 0;
  }

  ${Input}:checked ~ & span:nth-child(3) {
    transform: rotate(-45deg);
    top: 19.5px;
    left: 4px;
  }
`;

const Chip = styled.div`
  display: inline-block;
  font-size: 12px;
  font-weight: bold;
  padding: 4px;
  background: rgba(0,0,0,0.25);
  border-radius: 6px;
  box-shadow: inset 0 1px 2px rgba(0,0,0,0.25), 0 1px rgba(255,255,255,0.08);
  margin-rigth: 4px;
`;


const FolioCard: FC<IProps> = ({className, id}) => (
  <Holder className={className}>
    <Input id={`folio-${id}`} type="checkbox" />
    <ExpandButton htmlFor={`folio-${id}`}>
      <span></span>
      <span></span>
      <span></span>
    </ExpandButton>
    <Intro>
      <strong>FACEIT Web</strong>
      <div>
        <Chip>
          html/css
        </Chip>
        <Chip>
          react
        </Chip>
      </div>
    </Intro>
    <ContentHolder>
      <ImageHolder>
        <Image src={`/folio/${id}.jpg`} />
      </ImageHolder>
      <Description>
        He has appeared in more than 100 films and television shows,
        including The Deer Hunter, Annie Hall, The Prophecy trilogy, The Dogs of War ...
      </Description>
    </ContentHolder>
  </Holder>
)

export default FolioCard;
